import { Injectable, Inject } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';
import * as t from 'io-ts/lib/index';
import { switchMap, mapTo, map, share, shareReplay, catchError, tap } from 'rxjs/operators';
import { decode, decodeArray } from '../utils/decode';
import { Location } from '@angular/common';

import { RestService } from './rest.service';
import { TypeaheadRestService } from './typeahead-rest.service';
import { SETTINGS, Settings } from '../../settings';
import { TeacherSubstitution, TeacherSubstitutionFilter } from '../models/teacher-substitution.model';
import { number } from 'fp-ts';
import { TeacherResource } from '../models/teacher-resource.model';
import { AuthService } from './auth.service';
import { PaginatedEntriesService } from './paginated-entries.service';
import { LoadingService } from './loading-service';
import { ActivatedRoute } from '@angular/router';
import { formatISOLocalDate, formatISOUTC } from '../utils/date';

/**
 * Don't use this service to load presence types in
 * components/services, use the `PresenceTypesService` instead, that
 * caches these entries and loads them only once throughout the
 * application.
 */
@Injectable({
  providedIn: 'root',
})
export class TeacherSubstitutionService extends RestService<typeof TeacherSubstitution> {
  constructor(
      private loadingService: LoadingService,
      //private location: Location,
      private route: ActivatedRoute,
      http: HttpClient, 
      @Inject(SETTINGS) settings: Settings,
      public authService: AuthService
  ) {
    super(http, settings, TeacherSubstitution, 'TeacherSubstitutions');
    this.typeaheadService = new TypeaheadRestService(http, settings, TeacherResource, 'TeacherResources', 'FullName', 'TeacherId', 'Acronym');
  }

  typeaheadService:TypeaheadRestService<typeof TeacherResource>;
  substitutions$ = this.getList();
  mySubstitutions$:Observable<ReadonlyArray<t.TypeOf<typeof TeacherSubstitution>>> = 
    this.http.get<unknown>(`${this.baseUrl}/my`, {params: {'sort': 'DateFrom.asc'}}).pipe(
        switchMap(decodeArray(this.codec))
    );

  //getPaginatedList():PaginatedEntriesService<TeacherSubstitution, TeacherSubstitutionFilter> {
  //  return new PaginatedEntriesService<TeacherSubstitution, TeacherSubstitutionFilter>(this.location, this.loadingService, this.settings, '/teachersubstitutions')
  //}

  getMyList():Observable<ReadonlyArray<t.TypeOf<typeof TeacherSubstitution>>> {
    return this.http.get<unknown>(`${this.baseUrl}/my`, {params: {'sort': 'DateFrom.asc'}}).pipe(
        switchMap(decodeArray(this.codec))
    );
  }

  getCurrentList():Observable<ReadonlyArray<t.TypeOf<typeof TeacherSubstitution>>> {
    return this.http.get<unknown>(`${this.baseUrl}/current`).pipe(
        switchMap(decodeArray(this.codec))
    );
  }

  getAdminList():Observable<ReadonlyArray<t.TypeOf<typeof TeacherSubstitution>>> {
    let date = new Date();
    let date2 = new Date(Date.UTC(date.getUTCFullYear(), date.getUTCMonth(), date.getUTCDate()));
    let dateFromISO = new Date(date2.setUTCDate(date2.getUTCDate())).toISOString().split('.')[0];

    return this.http.get<unknown>(`${this.baseUrl}/`, {params: {limit: 9999, 'filter.IsRevoked': '=0', 'filter.DateTo': `!<${dateFromISO}`}}).pipe(
        switchMap(decodeArray(this.codec))
    );
  }

  edit(substitutionId:Number, dateFrom:Date, dateTo:Date, remarks:string):Observable<void> {
    let body = {
      'SubstitutionIds': substitutionId ? [substitutionId] : null,
      'DateFrom': formatISOLocalDate(dateFrom),
      'DateTo': formatISOLocalDate(dateTo),
      'Remarks': remarks
    };

    return this.http.put(`${this.baseUrl}/set`, body, {responseType:"json"}).pipe(
        mapTo(undefined), 
        share()
    );
  }
  create(holderId:number, substituteId:number, from:Date, to:Date, remarks:string):Observable<void> {
    let body = {
      'HolderId': holderId,
      'SubstituteIds': substituteId ? [substituteId] : null,
      'DateFrom': formatISOLocalDate(from),
      'DateTo': formatISOLocalDate(to),
      'Remarks': remarks
    }
    return this.http.put(`${this.baseUrl}/new`, body).pipe(mapTo(undefined), share());
  }
  createAdHoc(holderId:number):Observable<number> {
    console.log("createAdHoc" + holderId);
    let body = {
      'HolderId': holderId
    }
    return this.http.put<any>(`${this.baseUrl}/NewAdHoc`, body, {responseType: "json"}).pipe(map((v, i) => v.SubstitutionId as number), shareReplay());
  }
  delete(substitutionId:number):Observable<void> {
    let body = {
        'SubsititutionIds': substitutionId ? [substitutionId] : null
    };
    return this.http.put(`${this.baseUrl}/revoke`, body).pipe(mapTo(undefined), share());
  }

  start(substitutionId:number, webModuleRedirectUri:string|null=null):Observable<void> {
    let oAuthUrl = this.settings.oAuthUrl;
    let webmoduleRedirectUrl = webModuleRedirectUri ?? (window.location.pathname + window.location.hash);

    let body:any = {
      'access_token': this.authService.accessToken ?? "",
    };

    if (this.settings.oAuthRedirectUrl) {
      body['redirect_uri'] = this.settings.oAuthRedirectUrl;
    }

    let params = new URLSearchParams({
      'webModuleRedirectUri': webmoduleRedirectUrl,
    });
    let url = `${oAuthUrl}/Authorization/Substitutions/${substitutionId}/start?${params.toString()}`;
    
    this.postFormData(url, body);

    return new Observable<void>();
  }
  
  startAdHoc(substitutionId:number, redirect_uri:string|null=null):Observable<void> {
    return this.start(substitutionId, this.settings.webModuleRedirectUrlAdHoc);
  }

  stop(substitutionId:number):Observable<void> {
    let oAuthUrl = this.settings.oAuthUrl;
    let webmoduleRedirectUrl = window.location.pathname + window.location.hash;
    let body:any = {
      'access_token': this.authService.accessToken ?? "",
    };
    if (this.settings.oAuthRedirectUrl) {
      body['redirect_uri'] = this.settings.oAuthRedirectUrl;
    }

    let params = new URLSearchParams({
      'moduleRedirectUrl': window.location.hash.substring(1),
      'webModuleRedirectUri': webmoduleRedirectUrl,
    });
    let url = `${oAuthUrl}/Authorization/Substitutions/${substitutionId}/stop?${params.toString()}`;

    this.postFormData(url, body);

    return new Observable<void>();
  }

  private postFormData(url:string, data:any) {
    let formElement = document.createElement('form');
    formElement.setAttribute('target', '_self');
    formElement.setAttribute('action', url);
    formElement.setAttribute('method', 'POST');
    formElement.style.visibility = 'hidden';

    for (let [key, value] of Object.entries(data)) {
      let inputElement = document.createElement('input');
      inputElement.setAttribute('type', 'text');
      inputElement.setAttribute('name', key as string);
      inputElement.setAttribute('value', value as string);
      formElement.appendChild(inputElement);
    }

    document.body.appendChild(formElement);
    formElement.submit();
  }
}
