import { Injectable, Inject } from '@angular/core';
import { Location } from '@angular/common';
import { ActivatedRoute, Router } from '@angular/router';
import { BehaviorSubject, Observable } from 'rxjs';
import { catchError, mapTo, share, take, tap } from 'rxjs/operators';
import { SETTINGS, Settings } from 'src/app/settings';
import { StorageService } from './storage.service';
import { HttpClient } from '@angular/common/http';

export const SUBSTITUTE_ADMINISTRATOR_ROLE:string = 'SubstituteAdministratorRole';

@Injectable({
  providedIn: 'root',
})
export class AuthService {

  personId$:BehaviorSubject<Option<number>> = new BehaviorSubject<Option<number>>(null);
  holderId$:BehaviorSubject<Option<number>> = new BehaviorSubject<Option<number>>(null);
  substitutionId$:BehaviorSubject<Option<number>> = new BehaviorSubject<Option<number>>(null);

  constructor(
    private storage: StorageService, 
    private route: ActivatedRoute, 
    private router: Router,
    @Inject(SETTINGS) private settings: Settings,
    //private location: Location,
    private http: HttpClient
  ) {
    this.personId$.next(this.personId);
    this.substitutionId$.next(this.substitutionId);
    this.holderId$.next(this.holderId);
  }

  get isAuthenticated(): boolean {
    return Boolean(this.accessToken);
  }

  get accessToken(): Option<string> {
    return this.storage.getAccessToken();
  }

  get personId(): Option<number> {
    if (this.accessToken == null) return null;
    let stringId = this.parseJwt(this.accessToken).id_person;
    if (stringId == null) return null;
    return parseInt(stringId);
  }

  get personName(): Option<string> {
    if (this.accessToken == null) return null;
    return this.parseJwt(this.accessToken).fullname;
  }

  get substitutionId(): Option<number> {
    if (this.accessToken == null) return null;
    let substitutionId:Option<string> = this.parseJwt(this.accessToken).substitution_id;
    if (substitutionId == null) return substitutionId;
    return parseInt(substitutionId);
  }

  get holderId(): Option<number> {
    if (this.accessToken == null) return null;
    let substitutionId:Option<string> = this.parseJwt(this.accessToken).holder_id;
    if (substitutionId == null) return substitutionId;
    return parseInt(substitutionId);
  }

  get oAuthUrl(): Option<string> {
    if (this.accessToken == null) return null;
    return this.parseJwt(this.accessToken).iss;
  }

  get roles(): string[] {
    if (this.accessToken == null) return [];
    let rolesString = this.parseJwt(this.accessToken).roles ?? "";
    return rolesString.split(';');
  }

  public hasRole(role:string):boolean {
    return this.roles.includes(role);
  }

  public setTokenResponse(tokenResponse:any): void {
    this.storage.setToken(
      tokenResponse["access_token"], 
      tokenResponse["refresh_token"]
    );

    this.personId$.next(this.personId);
    this.substitutionId$.next(this.substitutionId);
    this.holderId$.next(this.holderId);
  }

  public setToken(accessToken:string): void {
    this.storage.setToken(
      accessToken, 
      ""
    );
    this.personId$.next(this.personId);
    this.substitutionId$.next(this.substitutionId);
    this.holderId$.next(this.holderId);
  }

  private parseJwt(token:string) {
    var base64Url = token.split('.')[1];
    var base64 = base64Url.replace(/-/g, '+').replace(/_/g, '/');
    var jsonPayload = decodeURIComponent(atob(base64).split('').map(function(c) {
      return '%' + ('00' + c.charCodeAt(0).toString(16)).slice(-2);
    }).join(''));

    return JSON.parse(jsonPayload);
  };

  public login():void {
    let oAuthUrl = this.settings.oAuthUrl;
    let instanceId = this.settings.instanceId;
    let clientId = this.settings.clientId;
    let redirectUrl = this.settings.oAuthRedirectUrl;
    let appScope = this.settings.appScope;

    let params = new URLSearchParams();
    if (!clientId) {
      console.error('clientId is not configured');
      return;
    }

    params.append('clientId', clientId);
    params.append('application_scope', appScope);
    if (redirectUrl) {
      params.append('redirectUrl', redirectUrl);
    }

    let loginUrl = `${oAuthUrl}/authorization/${instanceId}/login?${params.toString()}`;

    window.location.href = loginUrl;
  }

  public logout():Observable<void> {
    let oAuthUrl = this.settings.oAuthUrl;
    let instanceId = this.settings.instanceId;
    let body = `access_token=${encodeURIComponent(this.accessToken ?? "")}`;
    let logoutUrl = `${oAuthUrl}/authorization/${instanceId}/logout`;

    let result = this.http.post(logoutUrl, body, {responseType: 'text'}).pipe(
      tap(v => {
        console.log('response', v);
        this.personId$.next(null);
        this.holderId$.next(null);
        this.substitutionId$.next(null);
        this.storage.setToken(null, null);
        this.router.navigate(['/']);
      }),
      mapTo(undefined),
      share()
    );

    result.subscribe(v => console.log('success', v), v => console.log('errror', v));

    return result;
  }
}
