import { Injectable, Inject, Optional } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';
import * as t from 'io-ts/lib/index';
import { switchMap, mapTo, map, share, catchError, tap, first, onErrorResumeNext } from 'rxjs/operators';
import { decode, decodeArray } from '../utils/decode';

import { RestService } from './rest.service';
import { TypeaheadRestService } from './typeahead-rest.service';
import { SETTINGS, Settings } from '../../settings';
import { TeacherSubstitution } from '../models/teacher-substitution.model';
import { number } from 'fp-ts';
import { TeacherResource } from '../models/teacher-resource.model';
import { AuthService } from './auth.service';

/**
 * Don't use this service to load presence types in
 * components/services, use the `PresenceTypesService` instead, that
 * caches these entries and loads them only once throughout the
 * application.
 */
@Injectable({
  providedIn: 'root',
})
export class TeacherResourceService extends RestService<typeof TeacherResource> {
  constructor(
      http: HttpClient, 
      @Inject(SETTINGS) settings: Settings,
      public authService: AuthService
  ) {
    super(http, settings, TeacherResource, 'TeacherResources');
  }

  getTeacherName(id:Option<number>):Observable<Option<string>> {
    if (id == null) return new BehaviorSubject<Option<string>>(null);

    let params = {
      //'fields': ["Id", "FullName"].join(','),
      'filter.TeacherId': `=${id}`,
    }
    return this.http.get<unknown>(`${this.baseUrl}/`, {params: params}).pipe(
        tap(v => console.log('get  name', v), e => console.log('error1', e)),
        switchMap(v => {
          return decodeArray(this.codec)(v);
        }),
        tap(v => console.log('name received'), e => console.log('error2', e)),
        map(v => v[0].FullName),
    );
  }
}
