import * as t from 'io-ts';
import { DateFromISOString } from 'io-ts-types';
import { Reference, Option } from './common-types';

const TeacherSubstitution = t.type({
  Id: t.Int,
  HolderId: t.Int,
  Holder: t.string,
  SubstituteId: t.Int,
  Substitute: t.string,
  DateFrom: DateFromISOString,
  DateTo: DateFromISOString,
  Remarks: Option(t.string),
});
type TeacherSubstitution = t.TypeOf<typeof TeacherSubstitution>;
export { TeacherSubstitution };

export interface TeacherSubstitutionFilter {
  Holder: Option<string>;
  Substitute: Option<string>;
  DateFrom: Option<Date>;
  DateTo: Option<Date>;
}
