import { Component, ChangeDetectionStrategy, Inject, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { ToastrService } from 'ngx-toastr';
import { catchError, map, onErrorResumeNext, share, switchMap, tap } from 'rxjs/operators';
import { BehaviorSubject, combineLatest, EMPTY, Observable } from 'rxjs';

import { SETTINGS, Settings } from './settings';
import { I18nService } from './shared/services/i18n.service';
import { decode } from './shared/utils/decode';
import { NAVIGATOR } from './shared/tokens/dom-apis';
import { Router } from '@angular/router';
import { AuthService, SUBSTITUTE_ADMINISTRATOR_ROLE } from './shared/services/auth.service';
import { TeacherResourceService } from './shared/services/teacher-resource.service';
import { TeacherSubstitutionService } from './shared/services/teacher-substitution.service';

@Component({
  selector: 'app-standalone',
  templateUrl: './app-standalone.component.html',
  styleUrls: ['./app-standalone.component.scss'],
  changeDetection: ChangeDetectionStrategy.Default,
})
export class AppStandaloneComponent {

  isLoggedIn$:Observable<boolean>;
  isAdmin$:Observable<boolean>;
  userName$:Observable<Option<string>>;
  isSubstitutionActive$:Observable<boolean>;
  substitutionName$:Observable<Option<string>>;

  constructor(
    private router: Router,
    i18n: I18nService,
    public auth: AuthService,
    public location: Location,
    private teacherResourceService: TeacherResourceService,
    private teacherSubstitutionService: TeacherSubstitutionService,
  ) {
    this.isAdmin$ = new BehaviorSubject<boolean>(auth.hasRole(SUBSTITUTE_ADMINISTRATOR_ROLE));

    this.isLoggedIn$ = auth.personId$.pipe(
      map(v => !!v)
    );
    this.userName$ = auth.holderId$.pipe(
      map(v => this.auth.personName)
    );
    this.isSubstitutionActive$ = auth.substitutionId$.pipe(
      map(v => !!v)
    );
    this.substitutionName$ = auth.holderId$.pipe(
      tap(v => {
        console.log('load', v);
      }),
      switchMap(v => this.teacherResourceService.getTeacherName(v)),
      share(),
      tap(v => {
        console.log('name', v);
      }),
      onErrorResumeNext()
    );
  }

  stopSubstitution():void {
    if (this.auth.substitutionId != null) {
      this.teacherSubstitutionService.stop(this.auth.substitutionId).subscribe(v => null);
    }
  }

  login():void {
    this.auth.login();
  }

  logout():void {
    this.auth.logout();
  }
}
