import { Component, ChangeDetectionStrategy, Inject, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { ToastrService } from 'ngx-toastr';
import { catchError } from 'rxjs/operators';
import { EMPTY } from 'rxjs';
import { SETTINGS, Settings } from './settings';
import { I18nService } from './shared/services/i18n.service';
import { decode } from './shared/utils/decode';
import { NAVIGATOR } from './shared/tokens/dom-apis';
import { Router } from '@angular/router';
import { AuthService } from './shared/services/auth.service';

@Component({
  selector: 'app',
  template: '<div class="mt-2 pb-3"><router-outlet></router-outlet></div>',
  styleUrls: ['./app.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AppComponent implements OnInit {
  constructor(
    private router: Router,
    private auth: AuthService,
    private location: Location,
    i18n: I18nService,
    private toastrService: ToastrService,
    @Inject(SETTINGS) private settings: Settings,
    @Inject(NAVIGATOR) private navigator: Navigator
  ) {
    this.router.initialNavigation();
    i18n.initialize();
    this.checkSettings();
  }

  private checkSettings(): void {
    decode(Settings)(this.settings)
      .pipe(
        catchError((error) => {
          console.error(String(error));
          this.toastrService.error(
            'Please check the contents of the settings.js file (see Console output for more details).',
            'Invalid Settings'
          );
          return EMPTY;
        })
      )
      .subscribe();
  }

  ngOnInit(): void {
    let params = new URLSearchParams(window.location.search);

    let accessToken = params.get('access_token');
    let moduleRedirectUrl = params.get('moduleRedirectUrl');
    let hash = (window.location.hash ?? '').replace(/^\#\/?/, '');

    if (accessToken) {
      console.log('set token');
      this.auth.setToken(accessToken);
    }

    if (moduleRedirectUrl) {
      window.location.hash = `#${moduleRedirectUrl}`;
      //this.router.navigate([moduleRedirectUrl]);

    } else if (!hash) {
      console.log('navigate to assign');
      this.router.navigate(['substitutions', 'assign']);
    }

    if (accessToken || moduleRedirectUrl) {
      params.delete('access_token');
      params.delete('moduleRedirectUrl');

      window.location.search = params.toString();
    }
  }
}
