import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { switchMap, map } from 'rxjs/operators';
import * as t from 'io-ts';

import { Settings } from 'src/app/settings';
import { pick } from '../utils/types';
import { decodeArray, decode } from '../utils/decode';
import { RestService } from '../services/rest.service';
import { DropDownItem } from '../models/drop-down-item.model';

export class HttpParams {
  params: { [param: string]: string };
}

export interface TypeaheadService {
  getTypeaheadItems(
    term: string,
    additionalParams?: HttpParams
  ): Observable<ReadonlyArray<DropDownItem>>;
  getTypeaheadItemById(id: number): Observable<DropDownItem>;
}

export class TypeaheadRestService<T extends t.InterfaceType<any>>
  extends RestService<T>
  implements TypeaheadService {
  protected typeaheadCodec = t.type(
    pick(this.codec.props, [this.idAttr, this.labelAttr])
  );

  constructor(
    http: HttpClient,
    settings: Settings,
    codec: T,
    resourcePath: string,
    protected labelAttr: string,
    protected idAttr = 'Id',
    protected additionalLabelAttr: Option<string> = null
  ) {
    super(http, settings, codec, resourcePath);
  }

  getTypeaheadItems(
    term: string,
    additionalParams?: HttpParams
  ): Observable<ReadonlyArray<DropDownItem>> {
    let fields = this.getFields();
    const params = {
      params: {
        fields: fields.join(','),
        [`filter.${this.labelAttr}`]: `~*${term}*`,
      },
    };

    return this.http
      .get<unknown>(
        `${this.baseUrl}/`,
        additionalParams
          ? this.mergeHttpParams(params, additionalParams)
          : params
      )
      .pipe(
        switchMap(decodeArray(this.typeaheadCodec)),
        map((items) =>
          items.map((i) => {
            let value = i[this.labelAttr];
            if (this.additionalLabelAttr && i[this.additionalLabelAttr]) {
              value = `${value} (${i[this.additionalLabelAttr]})`;
            }
            return { Key: i[this.idAttr], Value: value }
          })
        )
      );
  }

  private getFields():string[] {
    let fields = [this.idAttr, this.labelAttr];
    if (this.additionalLabelAttr) {
      fields.push(this.additionalLabelAttr);
    }
    return fields;
  }

  getTypeaheadItemById(id: number): Observable<DropDownItem> {
    let fields = this.getFields();

    return this.http
      .get<unknown>(`${this.baseUrl}/${id}`, {
        params: {
          fields: fields.join(','),
        },
      })
      .pipe(
        switchMap(decode(this.typeaheadCodec)),
        map((item) => {
          let value = item[this.labelAttr];
          if (this.additionalLabelAttr && item[this.additionalLabelAttr]) {
            value = `${value} (${item[this.additionalLabelAttr]})`;
          }
          return { Key: item[this.idAttr], Value: value }
        })
      );
  }

  private mergeHttpParams(
    typeaheadParams: HttpParams,
    additionalParams: HttpParams
  ): HttpParams {
    const merged = {
      params: { ...typeaheadParams.params, ...additionalParams.params },
    };
    if (additionalParams.params.fields) {
      merged.params.fields = typeaheadParams.params.fields.concat(
        ',',
        additionalParams.params.fields
      );
    }
    return merged;
  }
}
