import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { NgbDateAdapter, NgbDateNativeAdapter, NgbDateParserFormatter } from '@ng-bootstrap/ng-bootstrap';
import { uniqueId } from 'lodash-es';
import { DateParserFormatter } from '../../services/date-parser-formatter';

@Component({
  selector: 'erz-date-select',
  templateUrl: './date-select.component.html',
  styleUrls: ['./date-select.component.scss'],
  providers: [
    { provide: NgbDateAdapter, useClass: NgbDateNativeAdapter },
    { provide: NgbDateParserFormatter, useClass: DateParserFormatter },
  ],
})
export class DateSelectComponent implements OnInit {
  ngbDate:Option<any>; 
  @Input('error') error:boolean = false;
  //@Input() 
  //get value():Option<Date> { return this.ngbDate; }
  //set value(val:Option<Date>) { this.ngbDate = val != null ? {year: val.getFullYear(), month: val.getMonth() + 1, day: val.getDate()} : null; }
  @Input() value: Option<Date> = null;
  @Input() placeholder = 'shared.date-select.default-placeholder';
  @Input() minDate: Option<Date> = null;
  @Output() valueChange = new EventEmitter<Option<Date>>();

  componentId = uniqueId('erz-date-select-');

  // The NgbDateNativeAdapter does not seem to be used for minDate on the
  // date-picker component. As a workaround, we convert to an NgbDate.
  get minNgbDate() {
    if (this.minDate) {
      let date = this.minDate;
      return {year: date.getFullYear(), month: date.getMonth() + 1, day: date.getDate()};
    } else {
      return null;
    }
  }

  constructor() {
  }

  ngOnInit(): void {}

  change(ngbDate:Option<any>):void{
    this.valueChange.emit(ngbDate);
    return;
    
    if (ngbDate != null) {
      //this.valueChange.emit(new Date(Date.UTC(ngbDate.year, ngbDate.month - 1, ngbDate.day)));
      this.valueChange.emit(ngbDate);
    } else {
      this.valueChange.emit(null);
    }
  }
}
