import { InjectionToken } from '@angular/core';
import * as t from 'io-ts';

import { Option } from './shared/models/common-types';

const Settings = t.type({
  apiUrl: t.string,
  oAuthUrl: t.string,
  oAuthRedirectUrl: t.union([t.string, t.undefined]),
  webModuleRedirectUrlAdHoc: t.union([t.string, t.undefined]),
  clientId: t.union([t.string, t.undefined]),
  instanceId: t.string,
  appScope: t.string,
  scriptsAndAssetsPath: t.string,

});

type Settings = t.TypeOf<typeof Settings>;
export { Settings };

declare global {
  interface Window {
    stellvertretung: {
      settings: Settings;
    };
  }
}

export const SETTINGS = new InjectionToken<Settings>('Application Settings', {
  providedIn: 'root',
  factory: () => window.stellvertretung.settings,
});
